.window {
  bottom: 16px;
  right: 16px;
  position: fixed;
  width: 320px;
  z-index: 1000;
  display: flex;
  max-height: calc(100vh - 32px);
}
.resizer {
  bottom: 16px;
  right: 16px;
  position: fixed;
  width: 320px;
  top: auto;
  z-index: 1000;
  margin-bottom: 0;
}
.windowMinimized {
  bottom: 16px;
  right: 16px;
  position: fixed;
  width: 320px;
  z-index: 1000;
  display: flex;
  max-height: calc(100vh - 32px);
  bottom: 0;
}
.resizerMinimized {
  bottom: 16px;
  right: 16px;
  position: fixed;
  width: 320px;
  top: auto;
  z-index: 1000;
  margin-bottom: 0;
  pointer-events: none;
  visibility: hidden;
}
.avatar {
  width: 40px;
  height: 40px;
  font-size: 40px;
  margin-right: 9px;
}
