.progress {
  appearance: none;
  background: transparent;
  cursor: pointer;
  width: 100%;
  height: 100%;
  user-select: none;
  outline-style: none;
  margin: 0;
  transition: width 0.9s ease-in;
}
.progress::-webkit-slider-runnable-track {
  background: #767676;
  height: 4px;
  border-radius: 2px;
}
.progress::-moz-range-track {
  background: #767676;
  height: 4px;
  border-radius: 2px;
}
.progress::-webkit-slider-thumb {
  appearance: none;
  width: 5px;
  height: 18px;
  border-radius: 4px;
  background-color: #d6d6d6;
  margin-top: -7px;
}
.progress::-moz-range-thumb {
  appearance: none;
  width: 5px;
  height: 18px;
  border-radius: 4px;
  background-color: #d6d6d6;
  margin-top: -7px;
}
.progress::placeholder {
  opacity: 0;
}
